import { OkerQueue } from './oker-queue';

export function OkerLimit (concurrency) {
	const queue = new OkerQueue();
	let activeCount = 0;

	const next = () => {
		activeCount--;

		if (queue.size > 0) {
			queue.pop()();
		}
	};

	const run = async (fn, resolve, args) => {
		activeCount++;

		const result = (async () => fn(...args))();

		resolve(result);

		try {
			await result;
		} catch {}

		next();
	};

	const enqueue = (fn, resolve, args) => {
		queue.push(run.bind(undefined, fn, resolve, args));

		(async () => {
			await Promise.resolve();

			if (activeCount < concurrency && queue.size > 0) {
				queue.pop()();
			}
		})();
	};

	const generator = (fn, ...args) => new Promise(resolve => {
		enqueue(fn, resolve, args);
	});

	Object.defineProperties(generator, {
		activeCount: {
			get: () => activeCount,
		},
		pendingCount: {
			get: () => queue.size,
		},
		clearQueue: {
			value: () => {
				queue.clear();
			}
		},
	});

	return generator;
}
